<template>
<div class="User-contract">
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Contract_No")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="7">
        <p>{{$t("common.Contract_name")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
        <Button type="primary" class="mgl-10">{{$t("common.Query")}}</Button>
      </Col>
      <Col :xs="12" :sm="12" :lg="5" style="line-height: 5;">
        <Button type="primary" class="mgl-10" @click="Add_contract">{{$t("common.Add_contract")}}</Button>
      </Col>
  </Row>
  <Table highlight-row :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  <Row type="flex" justify="space-around" class="code-row-bg page-data">
    <Col span="24">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row>
</div>
</template>

<script>
export default {
    name:'User_contract',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            columns1: [
                {
                    title: this.$t('common.Contract_No'),
                    key: 'contract_number',
                    align:'center'
                },
                {
                    title: this.$t('common.Contract_name'),
                    key: 'contract_name',
                    align:'center'
                },
                {
                    title: this.$t('common.Contract_category'),
                    key: 'contract_type',
                    align:'center'
                },
                {
                    title: this.$t('common.Create_Time'),
                    key: 'time',
                    align:'center'
                },
                {
                    title: this.$t('common.Submission_time'),
                    key: 'operation',
                    align:'center',
                    render: (h, params) => {
                      return h('div', [
                          h('Button', {
                              props: {
                                  type: 'primary',
                                  size: 'small'
                              },
                              style: {
                                  marginRight: '10px'
                              },
                              on: {
                                  click: () => {
                                      this.$router.push({
                                        path: '/Add_contract',
                                        name: 'Add_contract',
                                        params: {
                                            name: 'editorContent',
                                            send_data: this.editorContent
                                        }
                                      });
                                  }
                              }
                          }, this.$t("common.View")),
                          h('Button', {
                              props: {
                                  type: 'error',
                                  size: 'small'
                              },
                              on: {
                                  click: () => {
                                      this.remove(params.index)
                                  }
                              }
                          }, this.$t("common.Delete"))
                      ]);
                  }
                }
            ],
            data1: this.mockTableData1 (),
            Personal_info:null,
            editorContent:{
              name:'大公数据注册服务协议',
              use:'新用户注册签订协议',
              ban:true,
              content:'一、特别提示在此特别提醒您（用户）在注册成为***用户之前，请认真阅读本《***用户服务协议》（以下简称“协议”），确保您充分理解本协议中各条款。请您审慎阅读并选择接受或不接受本协议。除非您接受本协议所有条款，否则您无权注册、登录或使用本协议所涉服务。您的注册、登录、使用等行为将视为对本协议的接受，并同意接受本协议各项条款的约束。  本协议约定***与用户之间关于“***”服务（以下简称“服务”）的权利义务。“用户”是指注册、登录、使用本服务的个人。本协议可由***随时更新，更新后的协议条款一旦公布即代替原来的协议条款，恕不再另行通知，用户可在本APP中查阅最新版协议条款。在修改协议条款后，如果用户不接受修改后的条款，请立即停止使用***提供的服务，用户继续使用***提供的服务将被视为接受修改后的协议。二、账号注册1、用户在使用本服务前需要注册一个“***”账号。“***”账号应当使用手机号码绑定注册，请用户使用尚未与“***”账号绑定的手机号码，以及未被***根据本协议封禁的手机号码注册“***”账号。***可以根据用户需求或产品需要对账号注册和绑定的方式进行变更，而无须事先通知用户。  2、如果注册申请者有被***封禁的先例或涉嫌虚假注册及滥用他人名义注册，及其他不能得到许可的理由， ***将拒绝其注册申请。  3、鉴于“***”账号的绑定注册方式，您同意***在注册时将允许您的手机号码及手机设备识别码等信息用于注册。  4、在用户注册及使用本服务时，***需要搜集能识别用户身份的个人信息以便***可以在必要时联系用户，或为用户提供更好的使用体验。***搜集的信息包括但不限于用户的姓名、地址；***同意对这些信息的使用将受限于第三条用户个人隐私信息保护的约束。'
            }
        }
    },
    methods:{
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              data.push({
                  contract_number: 'DG2018031200'+i,
                  contract_name: parseInt(Math.random()*(3-1)+0)>0?'大公数据注册服务协议':'大公数据评级合同',
                  contract_type: parseInt(Math.random()*(3-1)+0)>0?'用户信用评级':'用户投资',
                  time:this.formatTime(this.getRandomDateBetween()),
                  operation:''
              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      formatTime (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          return y + '-' + m + '-' + d;
      },
      getMoble() {
          var prefixArray = new Array("130", "131", "132", "133", "135", "137", "138", "170", "187", "189");
          var i = parseInt(10 * Math.random());
          var prefix = prefixArray[i];

          for (var j = 0; j < 8; j++) {
              prefix = prefix + Math.floor(Math.random() * 10);
          }
          return prefix
      },
      onedata (data,index){
        //console.log(data,index)
        this.Personal_info = data
        //this.sendParams ()
      },
      sendParams () {
          this.$router.push({
              path: '/Userinfo',
              name: 'Userinfo',
              params: {
                  name: 'Personal-info',
                  send_data: this.Personal_info
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       },
      Add_contract (){
        this.$router.push('/Add_contract');
      },
      remove (index) {
        this.historyData.splice(index, 1);
      }
    },
    created(){
         this.handleListApproveHistory();
    }
}
</script>

<style lang="css">
.User-contract{
  
}
.User-contract .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
.User-contract .search_criteria p{
  height: 30px;
}
.User-contract .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  text-align: center;
  background-color: #fff;
}
</style>



